.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-bg-variant),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me:hover {
    animation: skew 2s infinite;
    transform: skew(10deg);
    animation-direction: alternate;
    opacity: .7;
}

@keyframes skew {
    0% {
      transform: skewX(10deg);
    }
    100% {
      transform: skewX(-10deg);
    }
}

.about__cards {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 2px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card p {
    font-size: 13px;
}

.about__card small {
    font-size: 0.8rem;
    color: var(--color-linear);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* Media Queries */

@media screen and (max-width: 980px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem; 
    }
    .about__container p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 480px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
}



